import * as React from 'react';
import styled, { keyframes } from 'styled-components';

const footerAnim = keyframes`
  0% {
    background-color: #000110;
  }
  10% {
    background-color: #261827;
  }
  20% {
    background-color: #182720;
  }
  30% {
    background-color: #272018;
  }
  40% {
    background-color: #143a52;
  }
  50% {
    background-color: #521438;
  }
  60% {
    background-color: #282b21;
  }
  70% {
    background-color: #2f1a16;
  }
  80% {
    background-color: #2d162f;
  }
  90% {
    background-color: #2f161b;
  }
`;

const FooterEl = styled.div`
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #000110;
  color: white;
  position: relative;
  animation: ${footerAnim} 30s infinite;
  & > h1 {
    font-family: 'Merriweather', serif;
    mix-blend-mode: difference;
  }
`;

class Footer extends React.Component {
  constructor(props) {
    super(props);

    this.wrapperRef = React.createRef();

    this.state = {
      stickToBottom: false
    }
  }

  componentDidMount () {
    const wrapper = this.wrapperRef.current;
    if (!wrapper) {
      return;
    }
    const { bottom } = wrapper.getBoundingClientRect();
    const { innerHeight } = window;
    if (bottom < innerHeight) {
      this.setState({ stickToBottom: true });
    }
  }

  render () {
    const { stickToBottom } = this.state;
    return (
      <FooterEl ref={ this.wrapperRef } className={ stickToBottom && 'stick' || '' }>
        <h1>МК, { new Date().getFullYear() } ©</h1>
      </FooterEl>
    );
  }
};

export default Footer;